.projeto {
    width: 100%;

    margin-top: 20rem;
    margin-bottom: 20rem;


    display: flex;
    justify-content: space-around;
    align-items: center;
}

.projeto #title {
    margin-top: 0;
    margin-bottom: 0;
}

.projeto #subtitle {
    margin-top: 30px;
    margin-bottom: 0;
}

.projeto img {
    max-width: 800px;
}

@media (max-width: 1200px) {
    .projeto {
        width: 90%;
        margin: 0 auto;

        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }

    .projeto img {
        margin-top: 5rem;
        margin-bottom: 5rem;
        max-width: 600px;
    }
}

@media (max-width: 600px) {
    .projeto {
        width: 80%;

        margin-top: 20rem;
        margin-bottom: 20rem;


        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }

    .projeto img {
        margin-top: 1rem;
        margin-bottom: 5rem;
        max-width: 200px;
    }
}